import request from '../../utils/request.js';
export default {
	data() {
		return {
			navIndex:0,
			categoryDatas:[]
		};
	},
	async mounted(){
		let result = await request('/getCategoryDatas');
		// console.log(result)
		this.categoryDatas=result;
	},
	methods:{
		changeNavIndex(index){
			this.navIndex=index;
		}
	},
	computed:{
		categoryObj(){
			return this.categoryDatas[this.navIndex]
		}
	}
}
